  overflow: hidden;
  margin: 0;
  padding: 0;
  text-indent: 0;
  list-style-type: none;
}

.ingredients-item {
  display: flex;
  align-items: center;
  height: var(--ingredients-item-height);
  padding: 0 0.7rem;
  background: var(--color-bg-tertiary);
  border-radius: var(--border-radius-200);
}
.ingredients-item-product_active {
  background: var(--color-bg-highlight);
  color: var(--color-text-highlight);
}
.ingredients-item:nth-child(n+9) {
  display: none;
}

body {
  font: var(--font-type-body);
  color: var(--color-text-primary);
}

.main {
  min-height: 100svh;
  background: var(--color-bg-primary);
}

.wrapper {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 8px;
}

.content {
  display: grid;
  row-gap: 10px;
  padding-top: 100px;
}

/* SEARCH */
.search {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  width: 100%;
}

.search__form {
  display: flex;
  position: relative;
  height: 2.8rem;
  background: var(--color-bg-tertiary);
  border-radius: var(--border-radius-200);
  overflow: hidden;
}

.search__search-icon {
  display: inline-block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 10px;
  width: 25px;
  height: 25px;
  margin: auto 0;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
  background-repeat: no-repeat;
  background-size: contain;
}

.search__input {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  background: transparent;
  padding: 0.8rem 0.8rem 0.8rem 3rem;
  font: inherit;
  color: inherit;
}
.search__input:focus {
  outline: none;
}

.search__button {
  display: none;
  height: 100%;
  border: 0;
  margin: 0;
}

/* FILTERS */
.filters-btns {
  width: 100%;
}

.filters__button {
  display: grid;
  place-items: center;
  height: var(--button-height-m);
  /* height: 100px; */
  aspect-ratio: 1/1;
  border: 0;
  padding: 0;
  background: var(--color-bg-tertiary);
  border-radius: var(--border-radius-200);
}

.filters__button-svg {
  /* background: #fff; */
}
.filters__button-svg * {
  fill: #fff;
}

