  position: sticky;
  top: 0;
  z-index: 10;
  padding: 1rem 1.5rem;
  background: var(--color-bg-secondary);
  font: var(--font-type-h3);
}

/* RECIPES */
.recipes {
  display: flex;
  flex-direction: column;
  row-gap: 0.75rem;
  width: 100%;
}

